@import 'grid.scss';

$primary-color: #445B63;
$secondary-color: #A3C4BC;
$link-color: #D9CFC1;

$font-body: "Sofia Sans";
$font-body-weight: 400;

$font-display: "Rise";

@mixin font($font-name, $font-weight) {
    font-family: $font-name, sans-serif;
    font-optical-sizing: auto;
    font-weight: $font-weight;
    font-style: normal;
}

@mixin stroke($fill-color, $stroke-color, $size) {
    -webkit-text-stroke-width: $size;
    -webkit-text-stroke-color: $stroke-color;
    color: $fill-color;  
}

@font-face { 
    font-family: Rise; 
    font-weight: 100;
    src: url('/fonts/ChevyRayRise.ttf');
} 

html {
    background-color: $primary-color;
}

body {
    color: $secondary-color;
    @include font($font-body, $font-body-weight);
    font-size: large;
    line-height: 1.5;
    margin: 1rem;
    max-width: 768px;
}

header {
    border-bottom: 1px solid $secondary-color;
    margin-bottom: 1rem;
}

h1 {
    @include font($font-display, 100);
    @include stroke($primary-color, $secondary-color, 2px)
}

h2 {
    @include font($font-display, 100);
    font-size: 1rem;
}

footer {
    border-top: 1px solid $secondary-color;
    margin-top: 1rem;
}

a {
    color: $link-color;
    text-decoration: underline;
} 
